import "../../assets/js/base.js"
import Prompt from '../tips/tankuang'
import store from '../../assets/js/shareData/share'
  export default {
    name: '',
    components:{
      Prompt
    },
    data() {
      return {
        showPsw:false,          //
        showPic:false,          //
        showCan:false,          //
        isEdit:false,
        user:{
          username:'',
          email:'',
          phoneNum:'',
          commonAddress:[
            {name:'',address:''}
          ],
          avatar:'touxiang_03.png'
        },  //
        inputPsw:'',
        flag:false,
        files:'',
        error:''
      }
    },
    mounted(){
      this.send();
    },
    methods:{
      send(){
        this.$http.get(this.$store.state.baseUrl+'getState').then(function (response) {
          let resData = response.data;
          if(resData){
            this.user = resData;
          }else{
            this.$router.push('/login');
          }
        })
      },
      edit(){
        this.showPsw=!this.showPsw;
      },
      showPicEdit(){
          this.showPic=!this.showPic;
      },
      showCancel(){
          this.showCan=!this.showCan;
      },
      //
      uploadAvatar(event){
          if(event.target.files.length>0){
            this.files = event.target.files[0];   //
            let data  = {};
            let files = this.files;
            let param = new FormData(); //form
            if(files!=''){
              param.append('file', files,files.name); //  append 
            }else{
              this.$message.error('');
            }
            param.append('param', JSON.stringify(data));//form
            let config = {
              headers:{'Content-Type':'multipart/form-data'}
            };  //
            this.$http.post(this.$store.state.baseUrl+'avatarUpload',param,config)
              .then(response => {
                let ret = response.data;
                if(ret.status){
                  //
                  this.user.avatar = ret.url;
                  this.files = [];
                  this.showPic=!this.showPic;
                }else{
                  alert('')
                }
              })
          }
      },
      //
      checkPassword(){
        if(this.inputPsw){
          this.$http.post(this.$store.state.baseUrl+"editPerInfo",{
            password:this.inputPsw
          },{emulateJSON: true}).then((response) => {
            if(response.data){
              this.flag=true;
              this.showPsw=!this.showPsw;
            }else {
              alert('');
            }
          })
        }else {
          alert('')
        }
      },
      //
      signOut(){
        this.$http.get(this.$store.state.baseUrl+"signOut").then((response) => {
          if(response.data){
            this.$router.push('/');
          }else {
            alert('');
          }
        });
      },
      //
      editUsername(){
        if(this.user.username){
          if(this.flag){
            this.$http.post(this.$store.state.baseUrl+"editUsername",{username:this.user.username},{emulateJSON: true}).then((response) => {
              alert(response.data);
            })
          }else {
            this.error = '';
            setTimeout(()=>{
              this.error='';
            },1500)
          }
        }else {
          alert('')
        }
      },
      //
      youxiangxiugai(){
        if(this.flag){
          return '/youxiangxiugai'
        }else {
          return ''
        }
      },
      xiugaimima(){
        if(this.flag){
          return '/xiugaimima'
        }else {
          return ''
        }
      },
      comAddress(){
        if(this.flag){
          return '/comAddress'
        }else {
          return ''
        }
      },
      zidingyi(){
        if(this.flag){
          return '/custom'
        }else {
          return ''
        }
      }
    },
    filters:{
      urlHandle(value,url){
        if(!value) return '';
        return url+'avatar/'+value;
      },
      addressFilter(value){
        if(value.length){
          return value[0].address;
        }else {
          return ''
        }
      }
    },
    store
  }



