import "../../assets/js/base.js"
import store from '../../assets/js/shareData/share'
  export default {
      name: '',
    data(){
      return{
        showPrice:false,
        qqHref:''
      }
    },
    created(){
      this.shareQQ();
    },
    methods:{
      shareWx(){
        this.$http.post(this.$store.state.baseUrl+"shareWx",{url:location.href}).then(response=>{
          let resData = response.data;
          wx.config({
            debug: true, // ,apialertpclogpc
            appId: resData.appId, // 
            timestamp: resData.timestamp, // 
            nonceStr: resData.nonceStr, // 
            signature: resData.signature,// 
            jsApiList: resData.jsApiList // JS
          });
          wx.ready(function(){
            wx.onMenuShareTimeline({
              title: '',
              link: 'localhost:8000',
              imgUrl:'',
              success: function () {
                // 
                alert('');
              },
              cancel: function () {
                // 
                alert('');
              }
            });
            wx.onMenuShareAppMessage({
              title: '',
              desc: '',
              link: 'localhost:8000',
              imgUrl: '',
              trigger: function (res) {
                // triggerajaxajax
              },
              success: function (res) {
                alert('');
              },
              cancel: function (res) {
                alert('');
              },
              fail: function (res) {
                alert(JSON.stringify(res));
              }
            });
          });
          wx.error(function(res){
            // configerrorconfigdebugresSPA
              console.log(res);
          });
        })
      },
      shareQQ(){
        var p = {
          url:location.href, /*URLQQ*/
          desc:'', /*(),|*/
          title:'', /*()*/
          summary:'', /*()*/
          pics:'', /*()*/
          flash: '', /*()*/
          site:'', /*() QQ*/
          style:'201',
          width:32,
          height:32
        };
        var s = [];
        for(var i in p){
          s.push(i + '=' + encodeURIComponent(p[i]||''));
        }
        this.qqHref = "http://connect.qq.com/widget/shareqq/index.html?',s.join('&'),'"
      }
    },
    store
  }



